@font-face {
    font-family: 'vanderchalkregular';
    src: url('../fonts/vanderchalk-webfont.eot');
    src: url('../fonts/vanderchalk-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/vanderchalk-webfont.woff2') format('woff2'),
         url('../fonts/vanderchalk-webfont.woff') format('woff'),
         url('../fonts/vanderchalk-webfont.ttf') format('truetype'),
         url('../fonts/vanderchalk-webfont.svg#vanderchalkregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

